@font-face {
	font-family: typewriter;
	src: url("./../fonts/JMH_Typewriter.ttf");
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	/* background-color: #ede9da; */
	background-color: #000000;
}

ul {
	list-style-type: square;
}

a:link,
a:hover,
a:visited,
a:active {
	color: white !important;
	text-decoration: underline;
}

.toggle {
	border: none;
}

.MainWrapper {
	color: white !important;
	justify-content: center;
}

.NavBarWrapper {
}

.Name {
	color: white !important;
	font-size: 2.5vh;
}

.NavBar {
	background: black;
	/* border-bottom: 2pt dashed; */
}

.navbar-toggler {
	color: white !important;
}

.NavItem {
	color: white !important;
	font-family: courier;
	font-size: 2vh;
	text-decoration: none !important;
}

.NavItem:hover {
	text-decoration: underline !important;
}

.NavItemActive {
	color: white !important;
	font-family: courier;
	font-size: 2vh;
	text-decoration: underline !important;
}

.content {
	padding-left: 2vw;
	padding-right: 2vw;
}

/* Source : https://css-tricks.com/snippets/css/typewriter-effect/ */

.typewriter {
	display: flex;
	width: auto;
}

.Greet {
	padding-top: 25vh;
	min-height: 100vh;
}

.Greet h1 {
	font-size: 4vh;
}

.Greet span {
	max-width: 100%;
	border-right: 0.5vh solid;
	font-size: 4vh;
	white-space: nowrap;
	animation: blink-caret 0.65s step-end infinite;
}

@keyframes blink-caret {
	from,
	to {
		border-color: transparent;
	}
	50% {
		border-color: white;
	}
}

.Experience {
	min-height: 100vh;
	padding-bottom: 15vh;
}

.Projects {
	min-height: 100vh;
}

.Projects h1 {
	padding-bottom: 3vh;
}

.Projects h2 {
	font-size: 3vh;
	text-align: center;
	text-transform: uppercase;
}

.Item {
	padding-top: 2vh;
	padding-bottom: 1vh;
}

.Item h4 span {
	color: white;
	font-style: normal;
	border-bottom: solid 1px white;
}

.Item p {
	color: #f0f0f0 !important;
}
